import { BrowserRouter as Router } from 'react-router-dom';

import AppRouter from './routers/AppRouter';
import { AuthProvider } from './auth/AuthProvider';
import Layout from './components/layout/Layout';

const App = () => (
    <>
        <Router basename="/seros">
            <AuthProvider>
                <Layout>
                    <AppRouter />
                </Layout>
            </AuthProvider>
        </Router>
    </>
);

export default App;
